<template>
  <div class="">
    <div class="tac">
      <van-button size="normal" @click.native="openConfirm">捕捉</van-button>
    </div>
  </div>
</template>

<script>
import { MessageBox } from 'mint-ui'
import { mapState } from 'vuex'
import { Button, Toast } from 'vant'
import service from '@/api'
export default {
  params: {
    petId: {
      type: String,
      default: '',
      required: true
    }
  },
  data () {
    return {
    }
  },
  methods: {
    openConfirm () {
      let _this = this
      if (!this.pet.number) {
        return
      }
      else {
        MessageBox.confirm('确定花费5个币捕捉吗？', '重要').then(action => {
          service.getCapture({
            petId: _this.pet.number
          }).then((data) => {
            if (data.success) {
              Toast({
                message: data.message + 'txhash 为：' + data.txhash,
                position: 'middle',
                duration: 1000
              })
            }
            else {
              Toast({
                message: data.message,
                position: 'middle',
                duration: 1000
              })
            }
          }).catch(e => {
            Toast({
              message: e.message,
              position: 'middle',
              duration: 1000
            })
          })
        })
      }
    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  },
  components: {
    VanButton: Button
  }
}
</script>

<style lang="scss" scoped>

</style>



