<template>
  <div class="">
    <div>
      <van-row>
        <van-col span="12">
          熊猫ID: {{pet.number}}
        </van-col>
        <van-col span="12">
          当前余额: {{balance}}
        </van-col>
      </van-row>
      <van-row>
        <van-col span="16" class="tal">
          <van-field
            v-model="value"
            label="训练金币个数"
            type="number"
            placeholder="输入金额"
            >
          </van-field>
        </van-col>
        <van-col span="8" class="tar">
          <van-button size="normal" @click.native="trainBtn">点我训练</van-button>
        </van-col>
      </van-row>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Field, Button, Row, Col, Toast } from 'vant'
import service from '@/api'

export default {
  data () {
    return {
      value: ''
    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet,
      balance: state => state.balance
    })
  },
  methods: {
    trainBtn () {
      let _this = this
      if (this.value !== '' && this.value <= this.balance) {
        service.getTrain({
          petId: _this.pet.number,
          amount: parseInt(_this.value, 10)
        })
        .then((data) => {
          if (data) {
            Toast({
              message: '训练成功生成 hash 为：' + data.txhash,
              position: 'middle',
              duration: 1000
            })
            this.$store.dispatch('setBalance', this.balance - parseInt(_this.value, 10))
            this.value = ''
          }
        })
        .catch((e) => {
          Toast({
            message: '请稍后再试！',
            position: 'middle',
            duration: 1000
          })
        })
      }
      else if (this.value === '') {
        Toast({
          message: '请输入金额',
          position: 'middle',
          duration: 1000
        })
      }
      else if (this.value > this.balance) {
        Toast({
          message: '余额不足，请充值！',
          position: 'middle',
          duration: 1000
        })
      }
    }
  },
  components: {
    VanField: Field,
    VanButton: Button,
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>
</style>



