<template>
  <div class="introduction">
    <span>名称: {{pet.name}}</span>
    <br>
    <span>出生日期: {{ pet.birth * 1000 | date }}</span>
    <br>
    <span>稀有属性值：<span v-if="pet.rare === 2">有</span><span v-else>无</span></span>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import filters from '@/filters'
export default {
  filters,
  data () {
    return {

    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  }
}
</script>

<style lang="scss" scoped>

</style>




