<template>
  <div class="">
    <ul>
      <li v-for="(item, index) in pet.miningrecord" :key="index">
        成长值增加值：{{item.growthup}}，掘金值：{{item.mining}}
      </li>
    </ul>
    <van-list
      class="trainrecord-con"
      v-model="loading"
      :finished="finished"
      @load="onLoad"
      >
      <van-row>
        <van-col span="8">交易ID</van-col>
        <van-col span="8">金额</van-col>
        <van-col span="8">时间</van-col>
      </van-row>
      <div v-for="(item, index) in data" :key="index">
        <van-row>
          <van-col span="8">{{ item.txid }}</van-col>
          <van-col span="8">{{ item.amount }}</van-col>
          <van-col span="8">{{ item.time * 1000 | dateTime }}</van-col>
        </van-row>
      </div>
    </van-list>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { List, Row, Col } from 'vant'
import filters from '@/filters'

export default {
  props: {
    data: {
      type: Array,
      default: []
    },
    total: {
      type: Number,
      default: 0
    },
    page: {
      type: Number,
      default: 0
    }
  },
  data () {
    return {
      loading: false,
      finished: false
    }
  },
  filters: filters,
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  },
  methods: {
    onLoad () {
      let _this = this
      let type = 4
      setTimeout(() => {
        _this.$emit('getTransaction', _this.page + 1, type)

        _this.loading = false

        if (_this.data.length >= _this.total) {
          _this.finished = true
        }
      }, 500)
    }
  },
  components: {
    VanList: List,
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>

</style>



