const zero = '0'

function padZero (value = '') {
  value = String(value)
  // return String(value).padStart(2, zero)
  return `${value.length > 1 ? '' : zero}${value}`
}

function dateTime (value = 0) {
  let date = new Date()
  let result = date.setTime(value)
  if (!result) return '-- --'

  let fullDate = `${date.getFullYear()}-${padZero(date.getMonth() + 1)}-${padZero(date.getDate())}`
  let time = `${padZero(date.getHours())}:${padZero(date.getMinutes())}:${padZero(date.getSeconds())}`
  return `${fullDate} ${time}`
}

function date (value = 0) {
  let date = new Date()
  if (Number.isNaN(+value)) {
    return value
  }
  date.setTime(value)

  return `${date.getFullYear()}-${padZero(date.getMonth() + 1)}-${padZero(date.getDate())}`
}

function percent (num, fixed = 1) {
  num = +num
  if (Number.isNaN(num)) {
    return '-'
  }
  return (num * 100).toFixed(fixed) + '%'
}

function numb (num, fixed = 1) {
  num = +num
  if (Number.isNaN(num)) {
    return '-'
  }
  return num.toFixed(fixed)
}

export default {
  dateTime,
  date,
  percent,
  numb
}



