<template>
  <div class="">
    <van-row>
      <van-col span="17" class="tal">
        <van-field
          v-model="value"
          label="出售金额"
          type="number"
          placeholder="请输入金额"
          >
        </van-field>
      </van-col>
      <van-col span="7" class="tac">
        <van-button size="normal" @click.native="sellBtn">出售</van-button>
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Field, Button, Row, Col, Toast } from 'vant'
import service from '@/api'

export default {
  data () {
    return {
      value: ''
    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  },
  methods: {
    sellBtn () {
      let _this = this
      if (this.value !== '') {
        service.getSell({
          petId: _this.pet.number,
          price: parseInt(_this.value, 10)
        })
        .then((data) => {
          if (data.success) {
            Toast({
              message: data.message + '出单 id 为：' + data.data.offerId,
              position: 'middle',
              duration: 1000
            })
          }
        })
        .catch((e) => {
          Toast({
            message: '请稍后再试！',
            position: 'middle',
            duration: 1000
          })
        })
      }
      else {
        Toast({
          message: '请输入金额',
          position: 'middle',
          duration: 1000
        })
      }
    }
  },
  components: {
    VanField: Field,
    VanButton: Button,
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>

</style>



