<template>
  <div>
    <h4 class="tac">族谱</h4>
    <br>
    <van-row>
      <van-col span="24">
        父代
      </van-col>
    </van-row>
    <van-row v-for="(it, i) in parent" :key="i">
      <van-col span="4">
        id: {{ it.number }}
      </van-col>
      <van-col span="20">
        name: {{it.name}}
      </van-col>
    </van-row>
    <br>
    <van-row>
      <van-col span="24">
        子代
      </van-col>
    </van-row>
    <van-row v-for="(it, idx) in children" :key="idx">
      <van-col span="4">
        id: {{it.number}}
      </van-col>
      <van-col span="20">
        name: {{it.name}}
      </van-col>
    </van-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { Row, Col } from 'vant'
export default {
  props: {
    parent: {
      type: Array,
      default: []
    },
    children: {
      type: Array,
      default: []
    },
    petAttr: {
      type: Array,
      default: []
    }
  },
  data () {
    return {
      loading: false,
      finished: false
    }
  },
  computed: {
    ...mapState({
      english: state => state.english,
      pet: state => state.pet
    })
  },
  components: {
    VanRow: Row,
    VanCol: Col
  }
}
</script>

<style lang="scss" scoped>

</style>



