<script>
import { mapState } from 'vuex'
import Introduction from '../components/pet/introduction'
import TrainRecord from '../components/pet/trainrecord'
import Benefit from '../components/pet/benefit'
import FamilyTree from '../components/pet/familytree'
import Train from '../components/pet/train'
import Sell from '../components/pet/sell'
import Capture from '../components/pet/capture'

export default {
  data () {
    return {
      petDetailTab: 'introduction',
      petBtnTab: 'train',
      petAttribute: [],
      petAttributeText: ['敏捷', '力量', '智力'],
      petAttributeTextEn: ['Agile', 'Force', 'Intelligence'],
      petAttr: [],
      parent: [],
      children: [],
      page: 0,
      perpage: 20,
      order: 'asc',
      sort: 'years',
      transactionSort: 'amount',
      transactionType: 0,
      petId: '',
      petTransaction: [],
      total: 0
    }
  },
  filters: {
    toFixed2 (value) {
      return (parseFloat(value)).toFixed(2)
    }
  },
  mounted () {
    let petNumber = this.pet.number ? this.pet.number : ''
    if (petNumber) {
      window.sessionStorage.setItem('petId', petNumber)
      this.petId = petNumber
    }
    else {
      this.petId = window.sessionStorage.getItem('petId') ? window.sessionStorage.getItem('petId') : ''
      if (this.petId) {
        this.getPet(this.petId)
      }
    }
    if (this.petId) {
      this.petAttributes()
      this.getTransaction()
    }
  },
  watch: {
    petId: function () {
      this.petId = this.pet.number
      this.getPet(this.petId)
    },
    petDetailTab: function (val) {
      switch (val) {
        case 'introduction':
          this.transactionType = 0
          this.getTransaction()
          break
        case 'trainrecord':
          this.transactionType = 3
          this.getTransaction()
          break
        case 'benefit':
          this.transactionType = 4
          this.getTransaction()
          break
        case 'familytree':
          this.transactionType = 0
          this.getTransaction()
          break
        default:
          this.transactionType = 0
          this.getTransaction
      }
    }
  },
  computed: {
    ...mapState({
      english: states => states.english,
      pet: states => states.pet
    })
  },
  methods: {
    backHome () {
      if (this.pet.isMarket) {
        this.$router.replace({name: 'Market'})
      }
      else {
        this.$router.replace({name: 'Home'})
      }
    },
    getPet (id) {
      let _this = this
      this.$store.dispatch('getPets', {
        params: {
          page: 0,
          perpage: 1,
          order: 'asc',
          sort: 'years',
          petId: id
        },
        cb: (err, data) => { // eslint-disable-line
          _this.$store.dispatch('setPet', data.info[0])
        }
      })
    },
    petAttributes () {
      let _this = this
      this.$store.dispatch('getPetAttribute', {
        params: {
          petId: _this.petId
        },
        cb: function (err, data) { // eslint-disable-line
          _this.petAttribute = data.attributes
          _this.parent = data.parent ? data.parent : []
          _this.children = data.children ? data.children : []
        }
      })
    },
    // 获取训练记录
    getTransaction (page, type) {
      let _this = this
      this.$store.dispatch('getTransaction', {
        params: {
          page: page ? page : _this.page,
          perpage: _this.perpage,
          order: _this.order,
          sort: _this.transactionSort,
          type: type ? type : _this.transactionType,
          petId: _this.pet.number
        },
        cb: (err, data) => { // eslint-disable-line
          if (data) {
            _this.total = data.total
            if (page === 0 || _this.page === 0) {
              _this.petTransaction = data.info ? data.info : []
            }
            else {
              _this.petTransaction = data.info ? [...data.info] : _this.petTransaction
            }
          }
        }
      })
    }
  },
  components: {
    Introduction,
    TrainRecord,
    Benefit,
    FamilyTree,
    Train,
    Sell,
    Capture
  }
}
</script>
<template>
  <div class="pet">

    <mt-header fixed :title="english ? 'pet detail' : '宠物详情'">
      <span slot="left"># {{ pet.number }}</span>
      <mt-button slot="right" class="goHome iconfont icon-shouye" @click.native="backHome"></mt-button>
    </mt-header>

    <div class="pet-detail panel">
      <div class="clearfix">
        <div class="fl">
          <img class="pet-img" :src="pet.imgurl" alt="">
        </div>
        <div class="fr">
          <ul>
            <li v-for="(item, index) in petAttribute" class="pet-attr" :key="index">
              <span>
                <span v-if="!english">{{ petAttributeText[index] }}</span>
                <span v-else>{{ petAttributeTextEn[index] }}</span>
              </span>
              : {{ item.value | toFixed2 }}
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="pet-tabs panel">
      <mt-navbar v-model="petDetailTab">

        <mt-tab-item id="introduction">
          <span>
            <span v-if="!english">简介</span>
            <span v-else>introduction</span>
          </span>
        </mt-tab-item>

        <mt-tab-item id="trainrecord">
          <span>
            <span v-if="!english">训练记录</span>
            <span v-else>train record</span>
          </span>
        </mt-tab-item>

        <mt-tab-item id="benefit">
          <span>
            <span v-if="!english">收益记录</span>
            <span v-else>benefit</span>
          </span>
        </mt-tab-item>

        <mt-tab-item id="familytree">
          <span>
            <span v-if="!english">族谱</span>
            <span v-else>family tree</span>
          </span>
        </mt-tab-item>

      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container class="tab-items" v-model="petDetailTab">

        <mt-tab-container-item id="introduction">
          <introduction 
            :petAttr="petAttr">
          </introduction>
        </mt-tab-container-item>

        <mt-tab-container-item id="trainrecord">
          <train-record 
            :data="petTransaction"
            :total="total"
            :page="page"
            :petAttr="petAttr"
            @getTransaction="getTransaction">
          </train-record>
        </mt-tab-container-item>

        <mt-tab-container-item id="benefit">
          <benefit 
            :data="petTransaction"
            :total="total"
            :page="page"
            :petAttr="petAttr"
            @getTransaction="getTransaction">
          </benefit>
        </mt-tab-container-item>

        <mt-tab-container-item id="familytree">
          <family-tree 
            :petAttr="petAttr"
            :parent="parent"
            :children="children">
          </family-tree>
        </mt-tab-container-item>

      </mt-tab-container>
    </div>

    <div class="pet-tabs panel" v-if="!pet.isMarket">
      <mt-navbar v-model="petBtnTab">
        <mt-tab-item id="train"><span>
          <span v-if="!english">训练</span>
          <span v-else>train</span></span>
        </mt-tab-item>
        <mt-tab-item id="sell"><span>
          <span v-if="!english">出售</span>
          <span v-else>sell</span></span>
        </mt-tab-item>
        <mt-tab-item id="capture"><span>
          <span v-if="!english">捕捉</span>
          <span v-else>capture</span></span>
        </mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container class="tab-items" v-model="petBtnTab">
        <mt-tab-container-item id="train">
          <train></train>
        </mt-tab-container-item>
        <mt-tab-container-item id="sell">
          <sell></sell>
        </mt-tab-container-item>
        <mt-tab-container-item id="capture">
          <capture></capture>
        </mt-tab-container-item>
      </mt-tab-container>

    </div>
  </div>
</template>
<style lang="scss" scoped>
.pet {
  .goHome {
    font-weight: bold;
    font-size: 16px;
  }
  .pet-detail {
    .fl,
    .fr {
      width: 50%;
      height: 160px;
    }
    .pet-img {
      margin-top: 20px;
      display: inline-block;
      height: 120px;
      width: 120px;
      background-color: #cdf5d4;
    }
    .pet-attr {
      margin-top: 3px;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
    }
  }
  .tab-items {
    margin: 20px 0;
  }
}
</style>


